import type { Document } from '@ali/4ever-cangjie';
import { Path } from '@ali/4ever-cangjie';

export function sortNodeKeys(keys: string[], document: Document) {
  return [...keys].sort((keyA, keyB) => {
    const pathA = document.getPath(keyA);
    const pathB = document.getPath(keyB);
    if (!pathA || !pathB) {
      return 0;
    }
    return Path.compare(pathA, pathB) || 0;
  });
}
